/*--------------*/
/* Base Styling */
/*--------------*/

.bio-page {
  margin: 5vh 0;
  background-color: white;
  border: solid 1px black;
  border-radius: 10px;
  padding: 2vh 5vw;
}

.bio-page h2 {
  text-decoration: underline;
  text-align: center;
}

.bio-link {
  text-decoration: none;
  color: rgb(29, 1, 131);
  transition-duration: 0.3s;
  font-weight: bold;
}

.bio-link:hover {
  color: #4e3edd;
}

/*----------------*/
/* Responsiveness */
/*----------------*/

/* Medium devices (tablets, 768px and up) */
@media screen and (min-width: 768px) {
  .bio-page h2 {
    font-size: 2em;
  }

  .bio-page p {
    font-size: 1.2em;
  }
}

/* Large devices (desktops, 992px and up) */
@media screen and (min-width: 992px) {
  .bio-page h2 {
    font-size: 2.3em;
  }

  .bio-page p {
    font-size: 1.3em;
  }
}
